//高德SDK
// import amap from '@/common/SDK/amap-wx.js';
export default {
	data() {
		return {
			showCategoryIndex: 0,
			headerPosition: "fixed",
			city: "北京",
			//分类列表
			twoList: [
				// {
				// 	id: 2,
				// 	text: '冰箱',
				// 	img: 'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=1275117093,2668160548&fm=26&gp=0.jpg',
				// 	desc: '11544'
				// },

			],
			categoryList: [
				// {
				// 	id: 1,
				// 	name: '家用电器',
				// },

			],
			selectlist: []
		}
	},
	onPageScroll(e) {
		//兼容iOS端下拉时顶部漂移
		if (e.scrollTop >= 0) {
			this.headerPosition = "fixed";
		} else {
			this.headerPosition = "absolute";
		}
	},
	onLoad() {
		this.getDataList()
		
	},
	methods: {
		//消息列表
		toMsg() {
			uni.navigateTo({
				url: '../msg/msg'
			})
		},
		getDataList() {
			this.$util.get('/xf/LabelTypeList').then(ress => {
				this.categoryList = ress.data
				this.$util.get('/xf/SecondList?id=' + ress.data[0].id).then(res => {
					this.twoList = res.data						
				})
				let obj = {
					...ress.data[0],
					serveList:[]
				}
				this.selectlist.push(obj)
				console.log(this.selectlist,'this.selectlist-onload')
			})
		},
		//分类切换显示
		showCategory(index, item) {
			let obj = {
				...item,
				serveList: []
			}
			this.selectlist.push(obj)
			console.log(this.selectlist,item, '545454');
			this.showCategoryIndex = index;
			this.$util.get('/xf/twoList?id=' + item.id).then(res => {
				this.twoList = res.data
			})
		},
		toCategory(item) {
			let index = this.showCategoryIndex
			console.log(this.selectlist, '00000000')
			// this.selectlist[index].serveList.push(item)
			this.selectlist[0].serveList.push(item)
			uni.setStorageSync('selectlist', this.selectlist)
			//uni.showToast({title: e.name,icon:"none"});
			uni.navigateTo({
				url: '/pages/publish/publish?pages=serveList'
			});
		},
		//搜索跳转
		toSearch() {
			uni.showToast({
				title: "建议跳转到新页面做搜索功能"
			});
		}
	}
}
